/**
 * Scope to hold object
 * @augments Scope
 * @class ObjectScope
 */
'use strict'

const Scope = require('./Scope')

/** @lends ObjectScope */
class ObjectScope extends Scope {
  /** Get a value for name */
  get (name) {
    const s = this
    return s.state[ name ]
  }

  /** Check value exists for name */
  has (name) {
    const s = this
    return s.state.hasOwnProperty(name)
  }

  static get initialState () {
    return {}
  }

  static get reducerFactories () {
    return {
      /**
       * Set property
       * @function ObjectScope#set
       * @param {string} name - name to set
       * @param {*} value - Value to set
       */
      set (name, value) {
        let byObject = arguments.length === 1
        let values = byObject ? arguments[ 0 ] : {
          [name]: value
        }
        return (state) => {
          let needsSet = Object.keys(values).some((name) => values[ name ] !== state[ name ])
          if (!needsSet) {
            return state
          }
          return Object.assign({}, state, values)
        }
      },
      /**
       * Delete property
       * @function ObjectScope#del
       * @param {...string} names - Name to delete
       */
      del (...names) {
        return (state) => {
          let needsToDelete = names.some((name) => state.hasOwnProperty(name))
          if (!needsToDelete) {
            return state
          }
          return Object.keys(state)
            .filter((filtering) => !names.includes(filtering))
            .reduce((reduced, name) => Object.assign(reduced, {
              [name]: state[ name ]
            }), {})
        }
      },

      /**
       * Delete all
       * @function ObjectScope#drop
       */
      drop () {
        return (state) => ({})
      }
    }
  }
}

module.exports = ObjectScope
