'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import {
  Route,
  Switch,
  Redirect
} from 'react-router-dom'
import { htmlAttributesFor } from 'the-component-util'

/**
 * Route of the-components
 */
class TheRoute extends React.PureComponent {
  render () {
    const s = this
    const { props } = s
    let {
      className,
      path,
      exact,
      strict,
      children,
      component,
      render
    } = props
    return (
      <div { ...htmlAttributesFor(props, { except: [ 'className' ] }) }
           className={ classnames('the-route', className) }>
        <Route { ...{ path, strict, exact, component, render } }>
          { children }
        </Route>
      </div>
    )
  }
}

TheRoute.propTypes = {
  /** Routing path */
  path: PropTypes.string,
  /** Exact match */
  exact: PropTypes.bool,
  /** Strict match */
  strict: PropTypes.bool,
  /** Component class */
  component: PropTypes.any,
  /** Render function */
  render: PropTypes.func
}

TheRoute.defaultProps = {}

TheRoute.displayName = 'TheRoute'

TheRoute.Switch = Switch
TheRoute.Redirect = Redirect
TheRoute.Status = ({ code, children }) => (
  <TheRoute render={({ staticContext }) => {
    if (staticContext) {
      staticContext.status = code
    }
    return children
  }}/>
)

export default TheRoute
