'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import { createMarkupForStyles } from 'react-dom/lib/CSSPropertyOperations'
import { EOL } from 'os'
import { ThemeValues } from 'the-component-constants'

/**
 * Style of the-components
 */
class TheStyle extends React.PureComponent {
  render () {
    const s = this
    const { props } = s
    let {
      id,
      type,
      className
    } = props
    return (
      <style className={ classnames('the-style', className) }
             { ...{ id, type }}
             dangerouslySetInnerHTML={ { __html: s.getInnerHTML() } }
      >
      </style>
    )
  }

  getInnerHTML () {
    const s = this
    return [
      s.getStylesAsString(),
      s.getChildrenAsString()
    ].filter(Boolean).join(EOL)
  }

  getStylesAsString () {
    const s = this
    let { styles } = s.props
    if (!styles) {
      return null
    }
    return Object
      .keys(styles)
      .map((selector) => TheStyle.styleString(selector, styles[ selector ]))
      .filter(Boolean)
      .join(EOL)
  }

  getChildrenAsString () {
    const s = this
    let { children } = s.props
    if (!children) {
      return null
    }
    return [].concat(children).map((child) => {
      return child
    }).join(EOL)
  }

  /**
   * Convert style into style string
   * @param {string} selector - Selector string
   * @param {Object} style - Style string
   * @returns {Object} Style object
   */
  static styleString (selector, style) {
    return [
      selector,
      '{',
      createMarkupForStyles(style, {
        _currentElement: {
          _owner: {
            $tmp: selector,
            getName: 'TheStyleTmp'
          }
        }
      }),
      '}'
    ].join(' ').trim()
  }

  static styles (values) {
    return Object(values)
  }
}

TheStyle.propTypes = {
  /** CSS class name */
  className: PropTypes.string,
  /** DOM Id */
  id: PropTypes.string,
  /** Script type */
  type: PropTypes.string
}

TheStyle.defaultProps = {
  className: null,
  id: null,
  type: null
}

TheStyle.displayName = 'TheStyle'

TheStyle.ThemeValues = ThemeValues

export default TheStyle
