'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import { BrowserRouter, StaticRouter, HashRouter } from 'react-router-dom'
import { htmlAttributesFor } from 'the-component-util'

/**
 * Router of the-components
 */
class TheRouter extends React.PureComponent {
  render () {
    const s = this
    const { props } = s
    let {
      className,
      basename,
      forceRefresh,
      getUserConfirmation,
      keyLength,
      children
    } = props
    return (
      <BrowserRouter { ...{
        basename,
        forceRefresh,
        getUserConfirmation,
        keyLength
      } }>
        <div { ...htmlAttributesFor(props, { except: [ 'className' ] }) }
             className={ classnames('the-router', className) }>
          { children }
        </div>
      </BrowserRouter>
    )
  }

  static Static ({ basename, location, context, children }) {
    return (
      <StaticRouter { ...{ basename, location, context } }>
        <div className='the-router-static'>
          { children }
        </div>
      </StaticRouter>
    )
  }

  static Hash ({ basename, getUserConfirmation, hashType, children }) {
    return (
      <HashRouter { ... { basename, getUserConfirmation, hashType } }>
        <div className='the-router-hash'>
          { children }
        </div>
      </HashRouter>
    )
  }
}

TheRouter.propTypes = {}

TheRouter.defaultProps = {}

TheRouter.displayName = 'TheRouter'

export default TheRouter
