'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import { htmlAttributesFor, eventHandlersFor } from 'the-component-util'

/**
 * ToastGroup
 */
class TheToastGroup extends React.PureComponent {
  render () {
    const s = this
    const { props } = s
    let {
      className,
      children
    } = props
    return (
      <div { ...htmlAttributesFor(props, { except: [ 'className' ] }) }
           { ...eventHandlersFor(props, { except: [] })}
           className={ classnames('the-toast-group', className) }
      >
        { children }
      </div>
    )
  }
}


TheToastGroup.propTypes = {}

TheToastGroup.defaultProps = {}

TheToastGroup.displayName = 'TheToastGroup'

export default TheToastGroup
