/**
 * Scope to hold number
 * @augments Scope
 * @class NumberScope
 */
'use strict'

const Scope = require('./Scope')

/** @lends NumberScope */
class NumberScope extends Scope {

  /**
   * Check the value is positive
   * @returns {boolean}
   */
  isPositive () {
    const s = this
    return s.state > 0
  }

  /**
   * Check the value is zero
   * @returns {boolean}
   */
  isZero () {
    const s = this
    return s.state === 0
  }

  /**
   * Check the value is negative
   * @returns {boolean}
   */
  isNegative () {
    const s = this
    return s.state < 0
  }

  static get initialState () {
    return 0
  }

  static get reducerFactories () {
    return {
      /**
       * Increment value
       * @function NumberScope#increment
       * @param {number} [amount=1]
       */
      increment (amount = 1) {
        return (state) => state + amount
      },
      /**
       * Decrement value
       * @function NumberScope#decrement
       * @param {number} [amount=1]
       */
      decrement (amount = 1) {
        return (state) => state - amount
      }
    }
  }
}

module.exports = NumberScope
