'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import TheStyle from 'the-style'
import { asStyleData } from 'the-component-util'

/** Style for TheIcon */
const TheIconStyle = ({ id, className, options }) => (
  <TheStyle { ...{ id } }
            className={ classnames('the-icon-style', className) }
            styles={ TheIconStyle.data(options) }
  />
)

TheIconStyle.displayName = 'TheIconStyle'
TheIconStyle.propTypes = {
  /** Style options */
  options: PropTypes.object
}

TheIconStyle.defaultProps = {
  options: {}
}

TheIconStyle.data = (options) => {
  const { ThemeValues } = TheStyle
  let {
    dominantColor = ThemeValues.dominantColor
  } = options
  return asStyleData('.the-icon', {
    '': {
      display: 'inline-block',
      margin: '0 2px'
    }
  })
}

export default TheIconStyle
