'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import TheStyle from 'the-style'
import { asStyleData } from 'the-component-util'

/** Style for TheButton */
const TheButtonStyle = ({ id, className, options }) => (
  <TheStyle { ...{ id } }
            className={ classnames('the-button-style', className) }
            styles={ TheButtonStyle.data(options) }
  />
)

TheButtonStyle.displayName = 'TheButtonStyle'
TheButtonStyle.propTypes = {
  /** Style options */
  options: PropTypes.object
}

TheButtonStyle.defaultProps = {
  options: {}
}

TheButtonStyle.data = (options) => {
  const { ThemeValues } = TheStyle
  let {
    dominantColor = ThemeValues.dominantColor,
    contentWidth = ThemeValues.contentWidth,
    backgroundColor = ThemeValues.backgroundColor,
    tappableHeight = ThemeValues.tappableHeight,
    overlayBackgroundColor = ThemeValues.overlayBackgroundColor,
    overlayTextColor = ThemeValues.overlayTextColor,
    disabledTextColor = ThemeValues.disabledTextColor,
    disabledBackgroundColor = ThemeValues.disabledBackgroundColor,
  } = options
  return Object.assign({},
    asStyleData('.the-button', {
      '&': {
        display: 'inline-flex',
        position: 'relative',
        textDecoration: 'none',
        textAlign: 'center',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'center',
        margin: '4px',
        boxSizing: 'border-box',
        padding: '0.5em 1em',
        maxWidth: contentWidth,
        minHeight: tappableHeight,
        border: `1px solid`,
        color: dominantColor,
        borderRadius: '4px',
        cursor: 'pointer',
        backgroundColor: backgroundColor,
        outlineColor: dominantColor
      },
      '&.the-button-primary': {
        backgroundColor: dominantColor,
        borderColor: dominantColor,
        color: backgroundColor
      },
      '&.the-button-simple': {
        backgroundColor: 'none',
        border: 'none'
      },
      '&.the-button-rounded': {
        borderRadius: '50%',
        borderWidth: '4px',
        width: '7em',
        height: '7em'
      },
      '&.the-button-floated': {
        boxShadow: '2px 2px 4px rgba(0,0,0,0.33)'
      },
      '&.the-button-wide': {
        width: '100%',
        margin: '4px 0'
      },
      '&.the-button-large': {
        fontSize: '2em'
      },
      '&.the-button-disabled': {
        pointerEvents: 'none',
        cursor: 'default',
        color: disabledTextColor,
        backgroundColor: disabledBackgroundColor
      },
      '.the-button-spinner': {
        position: 'absolute',
        left: 0,
        top: 0,
        right: 0,
        bottom: 0,
        zIndex: 1,
        backgroundColor: overlayBackgroundColor,
        borderColor: overlayBackgroundColor,
        color: overlayTextColor,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        overflow: 'hidden'
      },
      '.the-button-text': {
        display: 'inline-block',
        padding: '0 2px'
      },
      '.the-button-icon': {
        marginRight: '2px',
        marginLeft: '0'
      },
      '.the-button-icon-right': {
        marginLeft: '2px',
        marginRight: '0'
      }
    }),
    asStyleData('.the-button-group', {
      '&': {
        display: 'flex',
        margin: '8px 0',
        justifyContent: 'center',
        alignItems: 'center',
        flexWrap: 'wrap'
      },
      '&.the-button-group-collapsed': {
        flexWrap: 'nowrap',
        '.the-button': {
          margin: '0 -1px 0 0',
          width: '100%',
          paddingTop: '1em',
          paddingBottom: '1em',
          borderRadius: 0,
          borderBottom: 'none',
          borderRight: 'none',
          '&:first-child': {
            borderLeft: 'none',
            margin: '0'
          },
          '&:last-child': {
            borderRight: 'none'
          }
        }
      }
    })
  )
}

export default TheButtonStyle
