'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import TheIconStyle from './TheIconStyle'
import { htmlAttributesFor } from 'the-component-util'

/**
 * Icon of the-components
 */
class TheIcon extends React.PureComponent {
  render () {
    const s = this
    const { props } = s
    let {
      className,
      type,
      children
    } = props
    return (
      <i { ...htmlAttributesFor(props, { except: [ 'className' ] }) }
         aria-hidden
         className={ classnames('the-icon', type && `fa fa-${type}`, className) }
      >
        {children}
      </i>
    )
  }

  static Spin (props) {
    let { className } = props
    return (
      <TheIcon {...props}
               className={classnames('the-icon-spin', className, TheIcon.SPIN_ICON)}
      />
    )
  }
}

TheIcon.CDN_URL = 'https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css'

TheIcon.SPIN_ICON = 'fa fa-circle-o-notch fa-spin'

TheIcon.CdnLink = ({ url = TheIcon.CDN_URL }) => (
  <link rel='stylesheet' href={url}/>
)

TheIcon.Style = TheIconStyle

TheIcon.propTypes = {
  /** Type of icon */
  type: PropTypes.string
}

TheIcon.defaultProps = {
  type: null
}

TheIcon.displayName = 'TheIcon'

export default TheIcon
