/**
 * Locale resolver
 * @class TheLoc
 * @param {Object<string, Object>} config - Localed values
 */
'use strict'

const evaljson = require('evaljson')
const argx = require('argx')
const { sprintf } = require('sprintf')

const resolveInScope = (scope, keypath) => {
  let data = scope
  for (let key of keypath.split(/\./g)) {
    data = data[ key ]
    if (!data) {
      return null
    }
  }
  return data
}

/** @lends TheLoc */
class TheLoc {
  constructor (config) {
    const s = this
    for (let namespace of Object.keys(config || {})) {
      s.register(namespace, config[ namespace ], { ctx: config })
    }
  }

  /**
   * Register values for a namespace
   * @param {string} namespace
   * @param {Object} values
   * @param {Object} [options={}]
   * @returns {JkLoc} Returns this
   */
  register (namespace, values, options = {}) {
    const s = this
    s[ namespace ] = evaljson(values, options.ctx, s)
    return s
  }

  /**
   * Resolve message for key
   * @param {string} lang - Lang to resolve
   * @param {...string} keypaths - Key to resolve
   * @returns {Object}
   */
  resolve (lang, ...keypaths) {
    let args = argx(arguments)
    lang = args.shift('string')
    let vars = args.pop('object') || {}
    let keypath = args.remain().filter(Boolean).join('.')
    const s = this
    if (!s[ lang ]) {
      throw new Error('Unknown lang:' + lang)
    }
    let searchingLang = [
      lang,
      ...Object.keys(s).filter((key) => key !== lang)
    ]
    for (let lang of searchingLang) {
      let found = resolveInScope(s[ lang ], keypath)
      if (found) {
        return sprintf(found, vars)
      }
    }
    console.warn(`[TheLoc] Failed to resolve keypath: "${keypath}"`)
    return null
  }

  /**
   * Bind lang
   * @param {string} lang
   * @returns {function} - Bound function
   */
  bind (lang) {
    const s = this
    let scope = s[ lang ]
    if (!scope) {
      let defaultLang = Object.keys(s)[ 0 ]
      console.warn(`[TheLoc] lang "${lang}" is not found. Using "${defaultLang}" instead.`)
      scope = s[ defaultLang ]
    }
    let bound = s.resolve.bind(s, lang)
    return Object.assign(bound, scope)
  }

  toJSON () {
    const s = this
    return Object.assign(s)
  }

}

module.exports = TheLoc
