'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import TheStyle from 'the-style'
import { asStyleData } from 'the-component-util'

/** Style for TheToast */
const TheToastStyle = ({ id, className, options }) => (
  <TheStyle { ...{ id } }
            className={ classnames('the-toast-style', className) }
            styles={ TheToastStyle.data(options) }
  />
)

TheToastStyle.displayName = 'TheToastStyle'
TheToastStyle.propTypes = {
  /** Style options */
  options: PropTypes.object
}

TheToastStyle.defaultProps = {
  options: {}
}

TheToastStyle.data = (options) => {
  const { ThemeValues } = TheStyle
  let {
    contentWidth = ThemeValues.contentWidth,
    normalColor = ThemeValues.textColor,
    infoColor = ThemeValues.infoColor,
    warnColor = ThemeValues.warnColor,
    errorColor = ThemeValues.errorColor,
    activeOpacity = ThemeValues.activeOpacity,
    animationDuration = 100

  } = options

  let colorToast = (color) => ({
    '.the-toast-inner': {
      color: color,
      borderColor: color
    }
  })
  return Object.assign(
    asStyleData('.the-toast', {
      '&': {
        display: 'block',
        margin: '0 auto'
      },
      '&.the-toast-empty': {
        '.the-toast-inner': {
          paddingTop: 0,
          paddingBottom: 0,
          borderTopWidth: 0,
          borderBottomWidth: 0,
          margin: 0
        }
      },
      '.the-toast-inner': {
        maxWidth: contentWidth,
        margin: '4px auto',
        display: 'inline-block',
        textAlign: 'left',
        padding: '4px',
        boxShadow: '2px 2px 4px rgba(0,0,0,0.33)',
        backgroundColor: 'rgba(255, 255, 255, 0.95)',
        color: `${normalColor}`,
        border: `4px solid ${normalColor}`,
        fontSize: '14px',
        width: '240px',
        borderRadius: '2px',
        transition: `padding ${animationDuration}ms,border-width ${animationDuration}ms`,
        overflow: 'hidden'
      },
      '.the-toast-item': {
        display: 'block',
        padding: '2px 0',
        overflow: 'hidden',
        '&:active': {
          opacity: activeOpacity
        }
      },
      '.the-toast-item-icon': {
        display: 'inline-block',
        pointerEvents: 'none'
      },
      '.the-toast-text': {
        display: 'inline-block',
        margin: '0 2px',
        pointerEvents: 'none',
        cursor: 'default'
      },
      '&.the-toast-info ': colorToast(infoColor),
      '&.the-toast-warn ': colorToast(warnColor),
      '&.the-toast-error ': colorToast(errorColor),

      '.the-toast-transition-enter': {
        lineHeight: '0em'
      },
      '.the-toast-transition-enter-active': {
        lineHeight: '1em',
        padding: '2px 0',
        transition: `padding ${animationDuration}ms, line-height ${animationDuration}ms`
      },
      '.the-toast-transition-leave': {
        lineHeight: '1em',
        padding: '2px 0'
      },
      '.the-toast-transition-leave-active': {
        lineHeight: '0em',
        padding: '0',
        transition: `padding ${animationDuration}ms, line-height ${animationDuration}ms`
      }
    }),
    asStyleData('.the-toast-group', {
      '&': {
        position: 'fixed',
        height: 0,
        top: 'initial',
        overflow: 'visible',
        left: 0,
        bottom: 24,
        right: 0,
        textAlign: 'center',
        padding: 0,
        zIndex: 49,
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-end'
      }
    })
  )
}

export default TheToastStyle
