/**
 * HOC for form
 * @function withForm
 * @param {Component}
 */
'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import { newId } from 'the-component-util'
import { clone } from 'asobj'

/** @lends withForm */
function withForm (Component) {
  class WithForm extends React.Component {
    constructor (props) {
      super(props)
      const s = this
      s.theID = newId()

      s.handleUpdate = s.handleUpdate.bind(s)
      s.handleSubmit = s.handleSubmit.bind(s)

      s.bounds = {
        getInputAttributesOf: s.getInputAttributesOf.bind(s),
        getLabelAttributesOf: s.getLabelAttributesOf.bind(s),
        getFormAttributes: s.getFormAttributes.bind(s),
        getSubmitAttributes: s.getSubmitAttributes.bind(s)
      }
    }

    render () {
      const s = this
      const { props } = s
      let {
        children
      } = props
      return (
        <Component {...props}
                   {...s.bounds}
        >{children}</Component>
      )
    }

    requiredOf (name) {
      const s = this
      const { props } = s
      let { required } = props
      return [].concat(required).includes(name)
    }

    idOf (name) {
      const s = this
      const { props } = s
      let { id = s.theID } = props
      return [ id, name ].join('-')
    }

    /**
     * Get attribute for label
     * @param {string} name - Input name
     * @returns {Object} Attributes for label
     */
    getLabelAttributesOf (name) {
      const s = this
      return {
        htmlFor: s.idOf(name),
        required: s.requiredOf(name),
        'data-name': name
      }
    }

    /**
     * Get attribute for input
     * @param {string} name - Input name
     * @returns {Object} Attributes for input
     */
    getInputAttributesOf (name) {
      const s = this
      const { props, handleUpdate } = s
      let { values, errors } = props
      return {
        name,
        id: s.idOf(name),
        required: s.requiredOf(name),
        value: values[ name ],
        error: errors[ name ],
        onUpdate: handleUpdate
      }
    }

    getFormAttributes () {
      const s = this
      const { props } = s
      return clone(props, {
        without: [
          'values',
          'errors',
          'onUpdate',
          'onSubmit',
          'required',
          ...Object.keys(s.bounds)
        ]
      })
    }

    getSubmitAttributes () {
      const s = this
      const { handleSubmit } = s
      return {
        id: s.idOf('submit'),
        onSubmit: handleSubmit
      }
    }

    handleUpdate (values) {
      const s = this
      const { props } = s
      let { onUpdate } = props
      onUpdate && onUpdate(values)
    }

    handleSubmit () {
      const s = this
      const { props } = s
      let { onSubmit, values } = props
      onSubmit && onSubmit(values)
    }
  }

  WithForm.PropTypes = {
    /** DOM Id */
    id: PropTypes.string,
    /** Input values */
    values: PropTypes.object,
    /** Input errors */
    errors: PropTypes.object,
    /** Handler for input update */
    onUpdate: PropTypes.func,
    /** Handler for form submit */
    onSubmit: PropTypes.func,
    /** Required input names */
    required: PropTypes.arrayOf(PropTypes.string)
  }

  WithForm.defaultProps = {
    values: {},
    errors: {},
    required: []
  }

  return WithForm
}

export default withForm
