'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import TheRootStyle from './TheRootStyle'

import { htmlAttributesFor } from 'the-component-util'

/**
 * Root element of the-components
 */
class TheRoot extends React.PureComponent {
  render () {
    const s = this
    const { props } = s
    let {
      className,
      children
    } = props
    return (
      <div { ...htmlAttributesFor(props, { except: [ 'className' ] }) }
           className={ classnames('the-root', className) }
      >
        { children }
      </div>
    )
  }
}

TheRoot.Style = TheRootStyle

TheRoot.propTypes = {}

TheRoot.defaultProps = {}

TheRoot.displayName = 'TheRoot'

export default TheRoot
