'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import TheViewStyle from './TheViewStyle'
import { htmlAttributesFor, eventHandlersFor } from 'the-component-util'
import { TheLink } from 'the-link'
import { TheButton } from 'the-button'
import { TheContainer } from 'the-container'

/**
 * View of the-components
 */
class TheView extends React.PureComponent {
  render () {
    const s = this
    const { props } = s
    let {
      className,
      children
    } = props
    return (
      <div {...htmlAttributesFor(props, { except: [ 'className' ] })}
           {...eventHandlersFor(props, { except: [] })}
           className={classnames('the-view', className)}
      >
        {children}
      </div>
    )

  }

  static Header (props) {
    let {
      className,
      children,
      leftIcon,
      leftText,
      onLeftClick,
      leftTo,
      icon,
      text,
      rightIcon,
      rightText,
      onRightClick,
      rightTo,
    } = props

    return (
      <div {...htmlAttributesFor(props, { except: [ 'className' ] })}
           {...eventHandlersFor(props, { except: [] })}
           className={classnames('the-view-header', className)}
      >
        <TheContainer className='the-view-header-inner'>
          <span className='the-view-header-col'>
            <TheButton simple
                       icon={leftIcon}
                       text={leftText}
                       onClick={onLeftClick}
                       to={leftTo}
            />
          </span>
          <span className='the-view-header-col'>
            {icon && <TheIcon className={icon}/>}
            {text && <span>{text}</span>}
            {children && <span>{children}</span>}
          </span>
          <span className='the-view-header-col'>
            <TheButton simple
                       icon={rightIcon}
                       text={rightText}
                       onClick={onRightClick}
                       to={rightTo}
            />
          </span>
        </TheContainer>
      </div>
    )
  }

  static Body (props) {
    let {
      className,
      children
    } = props
    return (
      <div {...htmlAttributesFor(props, { except: [ 'className' ] })}
           {...eventHandlersFor(props, { except: [] })}
           className={classnames('the-view-body', className)}
      >
        <TheContainer className='the-view-body-inner'>
          {children}
        </TheContainer>
      </div>
    )
  }

  static Message (props) {
    let {
      className,
      children
    } = props
    return (
      <div {...htmlAttributesFor(props, { except: [ 'className' ] })}
           {...eventHandlersFor(props, { except: [] })}
           className={classnames('the-view-message', className)}
      >
        {children}
      </div>
    )
  }
}

TheView.BACK_ICON = 'fa fa-angle-left'
TheView.DROPDOWN_ICON = 'fa fa-ellipsis-v'

TheView.Style = TheViewStyle

TheView.propTypes = {}

TheView.defaultProps = {}

TheView.displayName = 'TheView'

export default TheView
