/**
 * Higher order component with route
 * @function withRoute
 * @param {function} Component - Component class
 * @returns {function} Wrapped component
 */
'use strict'

import React from 'react'
import PropTypes from 'prop-types'

const ContextTypes = {
  route: PropTypes.object
}

function withRoute (Component) {
  class WithRoute extends React.Component {
    render () {
      const s = this
      let { props, context } = s
      let { route = props.route } = context
      return <Component {...props}
                        {...{ route }}
      >{props.children}</Component>
    }
  }

  WithRoute.contextTypes = ContextTypes
  return WithRoute
}

export default withRoute
