'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import TheLinkStyle from './TheLinkStyle'
import { NavLink } from 'react-router-dom'
import { eventHandlersFor, htmlAttributesFor } from 'the-component-util'

/**
 * Link of the-components
 */
class TheLink extends React.PureComponent {
  render () {
    const s = this
    const { props } = s
    let {
      className,
      children,
      to,
      replace,
      exact,
      strict,
      activeClassName,
      activeStyle
    } = props
    return (
      <TheLink.Link { ...htmlAttributesFor(props, { except: [ 'className' ] }) }
                    { ...eventHandlersFor(props, { except: [] }) }
                    className={ classnames('the-link', className) }
                    activeClassName={ classnames('the-link-active', activeClassName) }
                    { ...{ replace, exact, strict, to, activeStyle }}
      >
        { children }
      </TheLink.Link>
    )
  }
}

TheLink.Style = TheLinkStyle

TheLink.Link = NavLink

TheLink.propTypes = {
  /** Link to */
  to: PropTypes.oneOfType([ PropTypes.string, PropTypes.object ]),
  /** Exact path */
  exact: PropTypes.bool,
  /** String path */
  strict: PropTypes.bool,
  /** Class name for active state */
  activeClassName: PropTypes.string,
  /** Style for active state */
  activeStyle: PropTypes.object
}

TheLink.defaultProps = {
  to: null,
  replace: false,
  exact: false,
  strict: false,
  activeClassName: null,
  activeStyle: {}
}

TheLink.displayName = 'TheLink'

export default TheLink
