'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import { TheIcon } from 'the-icon'
import TheFormStyle from './TheFormStyle'
import { htmlAttributesFor, eventHandlersFor } from 'the-component-util'

/**
 * Form of the-components
 */
class TheForm extends React.PureComponent {
  render () {
    const s = this
    const { props } = s
    let {
      className,
      children,
      spinning
    } = props
    return (
      <div {...htmlAttributesFor(props, { except: [ 'className' ] })}
           {...eventHandlersFor(props, { except: [] })}
           className={classnames('the-form', className)}
      >
        {children}
        {spinning && <TheForm.Spinner/>}
      </div>
    )
  }

  static Field (props) {
    const { className, children } = props
    return (
      <div {...htmlAttributesFor(props, { except: [ 'className' ] })}
           {...eventHandlersFor(props, { except: [] })}
           className={classnames('the-form-field', className)}
      >
        {children}
      </div>
    )
  }

  static Label (props) {
    const { className, children, required } = props
    return (
      <label {...htmlAttributesFor(props, { except: [ 'className' ] })}
             {...eventHandlersFor(props, { except: [] })}
             className={classnames('the-form-label', className, {
               'the-form-label-required': required
             })}
      >
        {children}
      </label>
    )
  }

  static Value (props) {
    const { className, children } = props
    return (
      <div {...htmlAttributesFor(props, { except: [ 'className' ] })}
           {...eventHandlersFor(props, { except: [] })}
           className={classnames('the-form-value', className)}
      >
        {children}
      </div>
    )
  }

  static Spinner (props) {
    return (
      <div className='the-form-spinner'>
        <TheIcon.Spin/>
      </div>
    )
  }
}

TheForm.Style = TheFormStyle

TheForm.propTypes = {
  spinning: PropTypes.bool
}

TheForm.defaultProps = {
  spinning: false
}

TheForm.displayName = 'TheForm'

export default TheForm
