/**
 * Higher order component with loc
 * @function withLoc
 * @param {function} Component - Component class
 * @returns {function} Wrapped component
 */
'use strict'

const React = require('react')
const PropTypes = require('prop-types')
const { createElement: c } = React

const ContextTypes = {
  lang: PropTypes.string,
  l: PropTypes.func
}

function withLoc (Component) {
  class WithLoc extends React.Component {
    render () {
      const s = this
      let { props } = s
      let { l, lang } = s.context
      return c(
        Component,
        Object.assign({}, props, { l, lang })
      )
    }
  }

  WithLoc.contextTypes = ContextTypes
  return WithLoc
}

withLoc.root = (Component, loc) => {
  class WithLocRoot extends React.Component {
    render () {
      const s = this
      let { props, l, lang } = s
      return c(
        Component,
        Object.assign({}, props, { l, lang })
      )
    }

    getChildContext () {
      const s = this
      let { lang, l } = s
      return { lang, l }
    }

    get l () {
      const s = this
      let { lang } = s
      return loc.bind(lang)
    }

    get lang () {
      const s = this
      let { lang } = s.props
      return lang
    }
  }
  WithLocRoot.childContextTypes = ContextTypes

  return WithLocRoot
}

module.exports = withLoc
