'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import TheToastStyle from './TheToastStyle'
import { CSSTransitionGroup } from 'react-transition-group'
import { htmlAttributesFor, eventHandlersFor } from 'the-component-util'

const normalizeMessages = (arr) => [].concat(arr)
  .filter(Boolean)
  .filter((e, i, arr) => i === arr.indexOf(e))

/**
 * Toast of the-components
 */
class TheToast extends React.PureComponent {
  constructor (props) {
    super(props)
    const s = this
    s._clearTimers = {}
  }

  render () {
    const s = this
    const { props } = s
    let {
      className,
      children,
      level,
      transitionTimeout,
      messages
    } = props
    messages = normalizeMessages(messages)
    let icon = TheToast.iconForLevel(level)
    return (
      <div { ...htmlAttributesFor(props, { except: [ 'className' ] }) }
           { ...eventHandlersFor(props, { except: [] })}
           className={ classnames('the-toast', className, `the-toast-${level}`, {
             'the-toast-empty': messages.length === 0
           }) }
      >
        <CSSTransitionGroup className='the-toast-inner'
                            component='div'
                            transitionName='the-toast-transition'
                            transitionEnterTimeout={ transitionTimeout }
                            transitionLeaveTimeout={ transitionTimeout }
        >
          {
            normalizeMessages(messages)
              .map((message) => (
                <div key={[ message ].join('-')}
                     className='the-toast-item'
                     data-message={message}
                     onClick={(e) => s.clearMessage(message)}
                >
                  <span className='the-toast-text'>
                    {icon && <i className={classnames('the-toast-text', icon)}/>}
                    {message}
                  </span>
                </div>
              ))
          }
          <div>{ children }</div>
        </CSSTransitionGroup>
      </div>
    )
  }

  clearMessage (message) {
    const s = this
    let { onUpdate, level, messages } = s.props

    onUpdate && onUpdate({
      [level]: messages.filter((filtering) => filtering !== message)
    })
  }

  componentDidMount () {
    const s = this
    s.reserveClearings()
  }

  componentDidUpdate () {
    const s = this
    s.reserveClearings()
  }

  componentWillUnmount () {
    const s = this
    for (let name of Object.keys(s._clearTimers)) {
      clearTimeout(s._clearTimers[ name ])
    }
  }

  reserveClearings () {
    const s = this
    let { messages, clearAfter } = s.props
    if (clearAfter > 0) {
      let messagesToClear = normalizeMessages(messages)
        .filter((message) => !s._clearTimers[ message ])
      for (let message of messagesToClear) {
        s._clearTimers[ message ] = setTimeout(() => {
          s.clearMessage(message)
          delete s._clearTimers[ message ]
        }, clearAfter)
      }
    }
  }

  static Normal (props) {
    return <TheToast {...props} level='normal'/>
  }

  static Info (props) {
    return <TheToast {...props} level='info'/>
  }

  static Warn (props) {
    return <TheToast {...props} level='warn'/>
  }

  static Error (props) {
    return <TheToast {...props} level='error'/>
  }

  static iconForLevel (level) {
    switch (level) {
      case 'info':
        return TheToast.INFO_ICON
      case 'warn':
        return TheToast.WARN_ICON
      case 'error':
        return TheToast.ERROR_ICON
      default:
        return null
    }
  }
}

TheToast.Style = TheToastStyle

TheToast.INFO_ICON = 'fa fa-check-circle'
TheToast.WARN_ICON = 'fa fa-warning'
TheToast.ERROR_ICON = 'fa fa-exclamation-circle'

TheToast.propTypes = {
  /** Level of toast */
  level: PropTypes.oneOf([
    'info',
    'normal',
    'warn',
    'error'
  ]),
  /** Handle update */
  onUpdate: PropTypes.func,
  /** Messages to show */
  messages: PropTypes.arrayOf(PropTypes.node),
  /** Clear message after seconds */
  clearAfter: PropTypes.number
}

TheToast.defaultProps = {
  level: 'normal',
  transitionTimeout: 100,
  clearAfter: -1
}

TheToast.displayName = 'TheToast'

export default TheToast
