'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'

import { TheBodyStyle } from 'the-body'
import { TheButtonStyle } from 'the-button'
import { TheContainerStyle } from 'the-container'
import { TheFooterStyle } from 'the-footer'
import { TheFormStyle } from 'the-form'
import { TheHeaderStyle } from 'the-header'
import { TheIconStyle } from 'the-icon'
import { TheInputStyle } from 'the-input'
import { TheLinkStyle } from 'the-link'
import { TheListStyle } from 'the-list'
import { TheMainStyle } from 'the-main'
import { TheMarkdownStyle } from 'the-markdown'
import { TheMenuStyle } from 'the-menu'
import { TheRootStyle } from 'the-root'
import { TheStyle } from 'the-style'
import { TheSectionStyle } from 'the-section'
import { TheToastStyle } from 'the-toast'
import { TheViewStyle } from 'the-view'

/** Theme Style of the-components */
const TheThemeStyle = ({ id, className, options }) => (
  <TheStyle { ...{ id } }
            className={ classnames('the-theme-style', className) }
            styles={ TheThemeStyle.data(options) }
  />
)

TheThemeStyle.displayName = 'TheThemeStyle'
TheThemeStyle.propTypes = {
  /** Style options */
  options: PropTypes.object
}

TheThemeStyle.defaultProps = {
  options: {}
}

TheThemeStyle.data = (options) => {
  return Object.assign({},
    ...[
      TheBodyStyle,
      TheButtonStyle,
      TheContainerStyle,
      TheFooterStyle,
      TheFormStyle,
      TheHeaderStyle,
      TheIconStyle,
      TheInputStyle,
      TheLinkStyle,
      TheListStyle,
      TheMainStyle,
      TheMarkdownStyle,
      TheMenuStyle,
      TheRootStyle,
      TheStyle,
      TheSectionStyle,
      TheToastStyle,
      TheViewStyle
    ].filter((Component) => Component.data).map((Component) => Component.data(options))
  )
}

export default TheThemeStyle
