'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import TheIcon from 'the-icon'
import TheButtonStyle from './TheButtonStyle'
import TheButtonGroup from './TheButtonGroup'
import { htmlAttributesFor, eventHandlersFor } from 'the-component-util'
import { TheLink } from 'the-link'

/**
 * Button of the-components
 */
class TheButton extends React.PureComponent {
  render () {
    const s = this
    const { props } = s
    let {
      className,
      children,
      primary,
      simple,
      wide,
      large,
      rounded,
      floated,
      spinning,
      disabled,
      to,
      icon,
      iconRight,
      text
    } = props
    const A = to ? TheLink : 'a'
    return (
      <A {...htmlAttributesFor(props, { except: [ 'className' ] })}
         {...eventHandlersFor(props, { except: [ 'onClick' ] })}
         onClick={ (e) => s.handleClick(e)}
         className={classnames('the-button', className, {
           'the-button-primary': primary,
           'the-button-spinning': spinning,
           'the-button-simple': simple,
           'the-button-wide': wide,
           'the-button-large': large,
           'the-button-rounded': rounded,
           'the-button-floated': floated,
           'the-button-disabled': disabled
         })}
         { ...{ to }}
         href={ !to && props.href || 'javascript:void(0)'}
         onKeyDown={ (e) => s.handleKeyDown(e) }
      >
        {spinning && <TheButton.Spinner/>}
        {icon && <TheIcon className={classnames('the-button-icon', icon)}/>}
        {text && <span className={classnames('the-button-text')}>{text}</span>}
        {children}
        {iconRight && <TheIcon className={classnames('the-button-icon', 'the-button-icon-right', iconRight)}/>}
      </A>
    )
  }

  handleClick (e) {
    const s = this
    const { onClick, onSubmit } = s.props
    onClick && onClick(e)
    onSubmit && onSubmit()
  }

  handleKeyDown (e) {
    const s = this
    const { onKeyDown, onSubmit } = s.props
    switch (e.keyCode) {
      case 32: // Space key
        e.preventDefault()
        onSubmit && onSubmit()
        break
      default:
        break
    }
    onKeyDown && onKeyDown(e)
  }

  static Spinner () {
    return (
      <span className='the-button-spinner'>
        <TheIcon.Spin className='the-button-spinner-icon'/>
      </span>
    )
  }
}

TheButton.Style = TheButtonStyle
TheButton.Group = TheButtonGroup

TheButton.propTypes = {
  /** Primary style */
  primary: PropTypes.bool,
  /** Simple style */
  simple: PropTypes.bool,
  /** Wide style */
  wide: PropTypes.bool,
  /** Large style */
  large: PropTypes.bool,
  /** Rounded style */
  rounded: PropTypes.bool,
  /** Floated style */
  floated: PropTypes.bool,
  /** Disabled state */
  disabled: PropTypes.bool,
  /** Show spinner */
  spinning: PropTypes.bool,
  /** Icon class */
  icon: PropTypes.string,
  /** Button text */
  text: PropTypes.string,
  /** Handle submit */
  onSubmit: PropTypes.func
}

TheButton.defaultProps = {
  primary: false,
  spinning: false,
  simple: false,
  wide: false,
  large: false,
  rounded: false,
  floated: false,
  disabled: false,
  icon: null,
  text: null,
  onSubmit: null
}

TheButton.displayName = 'TheButton'

export default TheButton
