'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import TheStyle from 'the-style'

/** Style for TheLink */
const TheLinkStyle = ({ id, className, options }) => (
  <TheStyle { ...{ id } }
            className={ classnames('the-link-style', className) }
            styles={ TheLinkStyle.data(options) }
  />
)

TheLinkStyle.displayName = 'TheLinkStyle'
TheLinkStyle.propTypes = {
  /** Style options */
  options: PropTypes.object
}

TheLinkStyle.defaultProps = {
  options: {}
}

TheLinkStyle.data = (options) => {
  const { ThemeValues } = TheStyle
  let {
    dominantColor = ThemeValues.dominantColor
  } = options
  return {
    '.the-link': {
      color: dominantColor,
      display: 'inline-block',
      padding: '0px 4px'
    },
    '.the-link-active': {
      textDecoration: 'none'
    }
  }
}

export default TheLinkStyle
