'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import TheStyle from 'the-style'
import { asStyleData } from 'the-component-util'

/** Style for TheForm */
const TheFormStyle = ({ id, className, options }) => (
  <TheStyle { ...{ id } }
            className={ classnames('the-form-style', className) }
            styles={ TheFormStyle.data(options) }
  />
)

TheFormStyle.displayName = 'TheFormStyle'
TheFormStyle.propTypes = {
  /** Style options */
  options: PropTypes.object
}

TheFormStyle.defaultProps = {
  options: {}
}

TheFormStyle.data = (options) => {
  const { ThemeValues } = TheStyle
  let {
    contentWidth = ThemeValues.contentWidth,
    overlayBackgroundColor = ThemeValues.overlayBackgroundColor,
    lightTextColor = ThemeValues.lightTextColor
  } = options
  return asStyleData('.the-form', {
    '&': {
      margin: '0 auto',
      maxWidth: contentWidth,
      position: 'relative'
    },
    '.the-form-field': {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'flex-start',
      flexWrap: 'wrap',
      padding: '0 8px',
      margin: '8px 0'
    },
    '.the-form-label': {
      fontSize: 'smaller',
      color: lightTextColor
    },
    '.the-form-value': {
      width: '100%',
      '.the-input-text': {
        margin: '0'
      }
    },
    '.the-form-spinner': {
      position: 'absolute',
      left: 0,
      top: 0,
      right: 0,
      bottom: 0,
      zIndex: 4,
      backgroundColor: overlayBackgroundColor,
      color: lightTextColor,
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      fontSize: '3em'
    }
  })
}

export default TheFormStyle
