/**
 * Wrap component with provider
 * @function withProvider
 * @param {function} Component
 * @param {TheStore} store
 */
'use strict'

const { createElement: c } = require('react')
const { Provider } = require('react-redux')

const storeFromProps = (props) => {
  let { store } = props
  if (!store) {
    throw new Error('store is required!')
  }
  return store
}

/** @lends withProvider */
function withProvider (Component) {
  function WithProvider (props) {
    return c(
      Provider,
      { store: storeFromProps(props) },
      c(Component, props)
    )
  }

  return WithProvider
}

module.exports = withProvider
