/**
 * Scoped redux store
 * @class TheStore
 * @param {Object} [options={}] - optional settings
 * @param {function} [options.reducer] - Custom reducer function
 * @param {string[]} [options.persists={}] - Scope names to persistize in local storage
 * @param {function} [options.middlewares=[]] - Redux middlewares
 */
'use strict'

const {
  applyMiddleware,
  create: createStore,
  combineReducers,
  devTool,
  persistize,
  hook
} = require('bredux')

const { scopes } = require('the-scope')
const toStoreScopeClass = require('./toStoreScopeClass')
const { setByNamepath } = require('./helpers')
const { unlessProduction } = require('the-check')

const NAMEPATH_SEPARATOR = '.'

/** @lends TheStore */
class TheStore {
  constructor (options = {}) {
    let {
      reducer: customReducer,
      state: preloadedState = {},
      persists = [],
      middlewares = [],
      enhancers = []
    } = options

    let restored = persistize.regain(persists) || {}
    let restoredKeys = Object.keys(restored || {})

    preloadedState = Object.assign({}, preloadedState, restored)

    const s = this
    s.scopes = {}
    s._preloadedState = preloadedState
    s._loadedReducers = restoredKeys.reduce((reducers, name) => Object.assign(reducers, {
      [name]: (state = restored[ name ]) => state
    }), {})

    const reducer = (state, action) => {
      if (customReducer) {
        state = customReducer(state, action)
      }
      for (let name of Object.keys(s._loadedReducers)) {
        let reducer = s._loadedReducers[ name ]
        let reduced = reducer(state[ name ], action)
        let changed = state[ name ] !== reduced
        if (changed) {
          state = Object.assign({}, state, {
            [name]: reduced
          })
        }
      }
      return state
    }
    let store = createStore(reducer, preloadedState, [
      devTool && devTool(),
      ...middlewares.map((middleware) => applyMiddleware(middleware)),
      ...enhancers
    ])

    hook(store, persistize(persists))

    Object.assign(s, store, { store })

    store.subscribe(() => {
      let state = store.getState()
      for (let name of Object.keys(s.scopes)) {
        let scope = s.scopes[ name ]
        scope.$$setState(state[ name ])
      }
    })
  }

  /**
   * Load a scope
   * @param {function} ScopeClass - Scope class to instantiate
   * @param {...string} names - Name key path
   * @returns {TheStore.Scope} Loaded scope
   */
  load (ScopeClass, ...names) {
    const s = this

    unlessProduction(({ ok }) => {
      ok(typeof ScopeClass !== 'string', 'ScopeClass must be an constructor')
      names.map((name) => ok(typeof name === 'string', 'Names must be string'))
    })

    const namepath = names.join(NAMEPATH_SEPARATOR)
    let duplicate = Boolean(s.scopes[ namepath ])
    if (duplicate) {
      throw new Error(`[TheStore] State with name "${namepath}" is already loaded!`)
    }
    let isReservedName = s[ namepath ] || [].includes(namepath)
    if (isReservedName) {
      throw new Error(`[TheStore] Failed to load state with name "${namepath}" because it is reserved`)
    }
    let {
      initialState = null,
      reducerFactories = {}
    } = ScopeClass

    if (s._preloadedState.hasOwnProperty(namepath)) {
      initialState = s._preloadedState[ namepath ]
    }

    const StoreScopeClass = toStoreScopeClass(ScopeClass, {
      name: namepath,
      dispatch: s.dispatch.bind(s),
      load: s.load.bind(s),
      initialState,
      reducerFactories: Object.assign({
        $$init (values) { return (state) => values }
      }, reducerFactories)
    })
    let scope = new StoreScopeClass(namepath)
    s.scopes[ namepath ] = scope
    setByNamepath(s, namepath, scope, NAMEPATH_SEPARATOR)
    s._loadedReducers[ namepath ] = StoreScopeClass.reducer
    scope.$$init(initialState)

    return scope
  }

  get state () {
    const s = this
    return s.getState()
  }
}

Object.assign(TheStore, scopes, {})

module.exports = TheStore
