'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import TheMarkdownStyle from './TheMarkdownStyle'
import { htmlAttributesFor, eventHandlersFor } from 'the-component-util'
import { Converter } from 'showdown'

/**
 * Markdown of the-components
 */
class TheMarkdown extends React.PureComponent {
  constructor (props) {
    super(props)
    const s = this
    s.converter = new Converter()
    s.state = {
      html: props.src && s.converter.makeHtml(props.src)
    }
  }

  render () {
    const s = this
    const { props } = s
    let {
      className,
      children
    } = props
    return (
      <div {...htmlAttributesFor(props, { except: [ 'className' ] })}
           {...eventHandlersFor(props, { except: [] })}
           className={classnames('the-markdown', className)}
           dangerouslySetInnerHTML={{ __html: s.state.html }}
      >
      </div>
    )
  }

  componentWillReceiveProps (nextProps) {
    const s = this
    let { src } = s.props
    let { src: nextSrc } = nextProps
    if (src !== nextSrc) {
      s.setState({
        html: nextSrc && s.converter.makeHtml(nextSrc)
      })
    }
  }
}

TheMarkdown.Style = TheMarkdownStyle

TheMarkdown.propTypes = {
  /** Markdown source string */
  src: PropTypes.string.isRequired
}

TheMarkdown.defaultProps = {
  src: null
}

TheMarkdown.displayName = 'TheMarkdown'

export default TheMarkdown
