/**
 * Abstract scope class
 * @abstract
 * @class Scope
 * @param {string} name - Name of state
 * @param {Object} config - Scope config
 */
'use strict'

const { unlessProduction } = require('the-check')

/** @lends Scope */
class Scope {
  constructor (name) {
    unlessProduction(({ ok }) => {
      ok(name, '[Scope] name is required!')
    })
    const s = this
    s.name = name
    s._state = undefined
  }

  get state () {
    const s = this
    return s._state
  }

  $$setState (state) {
    const s = this
    s._state = state
  }
}

module.exports = Scope
