'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import { htmlAttributesFor, eventHandlersFor } from 'the-component-util'

/**
 * Group of buttons
 */
class TheButtonGroup extends React.PureComponent {
  render () {
    const s = this
    const { props } = s
    let {
      className,
      collapsed,
      children
    } = props
    return (
      <div { ...htmlAttributesFor(props, { except: [ 'className' ] }) }
           { ...eventHandlersFor(props, { except: [] })}
           className={classnames('the-button-group', className, {
             'the-button-group-collapsed': collapsed
           })}
      >
        {children}
      </div>
    )
  }
}

TheButtonGroup.PropTypes = {
  /** collapsed style */
  collapsed: PropTypes.bool
}

TheButtonGroup.defaultProps = {
  collapsed: false
}

TheButtonGroup.displayName = 'TheButtonGroup'

export default TheButtonGroup
