'use strict'

import React from 'react'
import PropTypes from 'prop-types'
import classnames from 'classnames'
import TheStyle from 'the-style'
import { asStyleData } from 'the-component-util'

/** Style for TheMarkdown */
const TheMarkdownStyle = ({ id, className, options }) => (
  <TheStyle { ...{ id } }
            className={ classnames('the-markdown-style', className) }
            styles={ TheMarkdownStyle.data(options) }
  />
)

TheMarkdownStyle.displayName = 'TheMarkdownStyle'
TheMarkdownStyle.propTypes = {
  /** Style options */
  options: PropTypes.object
}

TheMarkdownStyle.defaultProps = {
  options: {}
}

TheMarkdownStyle.data = (options) => {
  const { ThemeValues } = TheStyle
  let {
    dominantColor = ThemeValues.dominantColor
  } = options
  return asStyleData('.the-markdown', {
    '&': {}
  })
}

export default TheMarkdownStyle
