/**
 * Higher order component with history
 * @function withHistory
 * @param {function} Component - Component class
 * @returns {function} Wrapped component
 */
'use strict'

import React from 'react'
import PropTypes from 'prop-types'

const ContextTypes = {
  history: PropTypes.object
}

function withHistory (Component) {
  class WithHistory extends React.Component {
    render () {
      const s = this
      let { props, context } = s
      let { history = props.history } = context
      return <Component {...props}
                        {...{ history }}
      >{props.children}</Component>
    }
  }

  WithHistory.contextTypes = ContextTypes
  return WithHistory
}

export default withHistory
