/**
 * @class TheClient
 */
'use strict'

const { SugoCaller } = require('sugo-caller/shim/browser')
const uuid = require('uuid')
const { get } = require('bwindow')
const { save, restore } = require('bstorage')
const argx = require('argx')
const co = require('co')

/** @lends TheClient */
class TheClient extends SugoCaller {
  constructor (...args) {
    super(...args)
    let {
      cid = TheClient.newCID()
    } = argx(args).pop('object') || {}
    const s = this
    s._rpc = null
    s._cid = cid
  }

  get scope () {
    const s = this
    return {
      /** Client ID */
      cid: s._cid,
      /** Host of client */
      host: get('location.host')
    }
  }

  /**
   * Use module
   * @param {string} moduleName - Module name
   * @returns {*}
   */
  use (moduleName) {
    const s = this
    return co(function * () {
      const rpc = s._rpc || (yield s.connect(TheClient.RPC_ACTOR_NAME))
      s._rpc = rpc
      return rpc.get(moduleName).with(s.scope)
    })
  }

  // noinspection ReservedWordAsName
  /**
   * Create the client instance
   * @param {string} namespace
   * @param {Object} [config={}]
   * @returns {TheClient}
   */
  static for (namespace = 'default', config = {}) {
    let storageKey = [ TheClient.CID_STORAGE_KEY, namespace ].join('/').trim()
    let cid = restore(storageKey) || TheClient.newCID()
    let client = new TheClient(Object.assign({}, config, { cid }))
    save(storageKey, cid)
    return client
  }
}

TheClient.RPC_ACTOR_NAME = 'rpc'
TheClient.CID_STORAGE_KEY = 'the:cid'

TheClient.newCID = () => uuid.v4()

module.exports = TheClient
