/**
 * Wrap component with store
 * @function withStore
 * @param {function} Component
 */
'use strict'

const React = require('react')
const { createElement: c } = React
const PropTypes = require('prop-types')

/** @lends withStore */
function withStore (Component) {
  class WithStore extends React.Component {
    render () {
      const s = this
      const { props, context } = s
      return c(
        Component,
        Object.assign({}, props, {
          store: context.store
        })
      )
    }
  }
  WithStore.contextTypes = {
    store: PropTypes.any
  }
  return WithStore
}

module.exports = withStore
