/**
 * Redux store for the-framework
 * @module the-store
 */
'use strict'

const { Provider, connect } = require('react-redux')
const TheStore = require('./TheStore')
const { scopes } = require('the-scope')
const create = require('./create')
const withProvider = require('./withProvider')
const withStore = require('./withStore')
const lib = create.bind(this)

Object.assign(lib, TheStore, scopes, {
  TheStore,
  create,
  Provider,
  withProvider,
  withStore,
  connect
})

module.exports = lib
