/**
 * Higher order component with client
 * @function withClient
 * @param {function} Component - Component class
 * @returns {function} Wrapped component
 */
'use strict'

const React = require('react')
const PropTypes = require('prop-types')
const { createElement: c } = React

const ContextTypes = {
  client: PropTypes.any
}

function withClient (Component) {
  class WithClient extends React.Component {
    render () {
      const s = this
      let { props } = s
      let { client } = s.context
      return c(
        Component,
        Object.assign({}, props, { client })
      )
    }
  }

  WithClient.contextTypes = ContextTypes
  return WithClient
}

withClient.root = (Component) => {
  class WithClientRoot extends React.Component {
    render () {
      const s = this
      let { props, client } = s
      return c(
        Component,
        Object.assign({}, props, { client })
      )
    }

    getChildContext () {
      const s = this
      let { client } = s.props
      return { client }
    }
  }
  WithClientRoot.childContextTypes = ContextTypes

  return WithClientRoot
}

module.exports = withClient
